import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Pagination, PaginationItem, PaginationLink, Row, Table } from 'reactstrap';
import { Button, Input, InputGroup, InputGroupAddon, InputGroupText, Alert } from 'reactstrap';
import { CardGroup, Container, Form, FormGroup, Fade, Collapse, Label, ListGroup, ListGroupItem, ListGroupItemHeading, ListGroupItemText } from 'reactstrap'
import { Popover, PopoverBody, PopoverHeader } from 'reactstrap'
import { createPeerId, getFileHost, getCurrentHost, calcAccountId } from '../js/gegeUtil.js'
import { doBShopTransaction } from '../../eshop/eshop.js'
const ShareManager = require('../js/share').default
const Resumable = require('resumablejs')

const get = require('simple-get')
const { shareResource, postSafe, loadLocalAccount, signString } = require('../js/gegeUtil')
const torrentPoster = require('./torrent-poster')


class ResourceShare extends Component {

  constructor(props) {
    super(props);
    let res = { tags: '', price: '', fee: '', deadline: '', owner: '', id: '' }
    this.state = { collapse: true, resourceList: [], resource: res, rsId: '账号未登陆' }
    this.state.shareMsg = '';
    this.share = new ShareManager(this.state);
    this.userSec = ''
    this.fee = 0.01;
    this.deadline = 5;
    this.price = 0;
    this.tags = ''
    this.resName = ''
    this.resId = ''
    this.infoInterval = null
    this.torrentList = []
    this.resume = null;
    this.html5File = null;
    this.torrent = null;
    this.poster = null;
  }



  cancelShare() {
    this.setState({ collapse: !this.state.collapse });
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  uploadTorrent() {
    //upload torrent to server
    let torrent = this.torrent;
    let torrentFile = torrent.torrentFile.toString('base64');
    let infoHash = torrent.infoHash;
    let poster = this.poster;
    let name = this.torrent.files[0].name
    this.setState({ shareMsg: '文件上传中...' })
    console.log('upload torrentFile' + torrentFile);
    //TODO upload poster
    let req = { torrentFile, name };
    let exp = 1000 * 60 * 60 * 24;
    let time = new Date().getTime()
    time = time - (time % exp);
    let signature = signString(this.userSec, infoHash + time);
    let host = getFileHost()
    //host = 'http://127.0.0.1:18080'
    let url = host + '/torrentFile/' + infoHash + '/' + signature
    let config = { url, nonce: '0000' }
    postSafe(config, req, (res) => {
      if (res) {
        this.initResumable(infoHash, signature)
        this.resume.addFile(this.html5File);
      }
    })
  }

  uploadAgain() {
    this.setState({ shareMsg: '资源上传中...', uploadAgain: false })
    this.resume.addFile(this.html5File);
  }

  doShare() {
    let deadline = this.deadline + '';
    let fee = this.fee + '';
    let resName = this.resName;
    let price = this.price + ''
    let pid = createPeerId(this.userSec);
    let pk = pid.pk;
    let accountId = pid.aid;
    let tags = new Buffer(this.tags + '$$' + this.resName).toString('hex');
    var data = { action: 'transaction', type: 'share', resId: this.resId, accountId, pk, fee, deadline, price, resName: tags };
    let parameters = { tags }
    doBShopTransaction(this.userSec, data, (ok, msg) => {
      if (ok && msg && msg.ok) {
        this.setState({ shareMsg: '资源校验成功，资源上传中,请稍后...', ready: false })
        setTimeout(() => {
          this.uploadTorrent()
        }, 3000);
      } else {
        this.setState({ shareMsg: '发布资源出错了' })
      }
    }, parameters)
  }
  //   shareResource(this.resId, this.userSec, this.fee, this.deadline, this.price, this.tags, this.resName, (ok) => {
  //     if (!ok) {
  //       this.setState({ shareMsg: '发布资源出错了' })
  //     } else {
  //       this.setState({ shareMsg: '资源校验成功，资源上传中,请稍后...' })
  //       this.uploadTorrent()
  //     }
  //   })
  // }

  componentDidMount() {
    var account = loadLocalAccount();
    if (account && account.secret) {
      this.userSec = account.secret;
      this.setState({ rsId: account.rsId })
    }
    this.share.init(this.onTorrentDone.bind(this), this.onTorrentStart.bind(this))
  }

  initResumable(infoHash, signature) {
    let host = getFileHost()
    // host = 'http://127.0.0.1:18080'
    let target = host + '/share/' + infoHash + '/' + signature;
    //console.log('target=' + target)
    this.resume = new Resumable({
      target,
      chunkSize: 10 * 1024 * 1024,
      simultaneousUploads: 4,
      testChunks: false,
      throttleProgressCallbacks: 1,
      method: "octet"
    });
    //
    this.resume.on('fileAdded', ((file) => {
      console.log('resume got file ' + file)
      if (this.resume)
        this.resume.upload()
      else {
        console.log('this.resume is null')
      }
    }).bind(this))
    this.resume.on('pause', function () {
      console.log('resume pause')
    });
    this.resume.on('complete', () => {
      console.log('resume complete')
      if (!this.state.uploadAgain)
        this.setState({ shareMsg: '文件上传成功' })
    });
    this.resume.on('fileSuccess', (file, message) => {
      console.log('resume fileSuccess')
    });
    this.resume.on('fileError', (file, message) => {
      console.log('resume fileError')
      this.setState({ shareMsg: '发布资源出错了', uploadAgain: true })
    });
    this.resume.on('fileProgress', (file) => {
      let p = file.progress() * 100;
      this.setState({ shareMsg: '文件上传中 ' + p + '%' })
    });
    this.resume.on('cancel', function () {
      console.log('resume cancel')
    });
    this.resume.on('uploadStart', function () {
      console.log('resume uploadStart')
    });
  }

  prettyBytes(num) {
    var exponent, unit, neg = num < 0, units = ['B', 'kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
    if (neg) num = -num
    if (num < 1) return (neg ? '-' : '') + num + ' B'
    exponent = Math.min(Math.floor(Math.log(num) / Math.log(1000)), units.length - 1)
    num = Number((num / Math.pow(1000, exponent)).toFixed(2))
    unit = units[exponent]
    return (neg ? '-' : '') + num + ' ' + unit
  }

  updateTorrentStatus() {
    //console.log('size ' + this.torrentList.length)
    this.torrentList.map((torrent) => {
      //uploadSpeed peers
      let peersSpan = document.querySelector('#pid' + torrent.infoHash);
      if (peersSpan)
        peersSpan.innerHTML = torrent.numPeers;
      let speedSpan = document.querySelector('#sid' + torrent.infoHash);
      if (speedSpan)
        speedSpan.innerHTML = this.prettyBytes(torrent.uploadSpeed) + '/s';
    })
  }


  onTorrentStart(html5Files) {
    this.setState({ shareMsg: '处理中...(大文件可能会比较慢,请耐心等待)' })
    if (html5Files) {
      this.html5File = html5Files[0];
    }
  }

  onTorrentDone(torrent) {
    this.setState({ ready: true })
    if (torrent) {
      this.torrent = torrent;
      var info = 'Torrent info hash: ' + torrent.infoHash +
        'magnetURI=' + torrent.magnetURI +
        ' torrentFileBlobURL="' + torrent.torrentFileBlobURL + 'name=' + torrent.name;
      //console.log(info);
      this.resId = torrent.infoHash
      this.resName = torrent.name;
      var rlist = this.state.resourceList;
      rlist.push({ id: this.resId, name: this.resName })
      this.setState({ resourceList: rlist })
      torrent.key = torrent.infoHash;
      //tags is not set yet
      //torrent.tags = this.tags;
      this.torrentList.unshift(torrent)
      // torrentPoster(torrent, (err, buf, extension) => {
      //   if (err) {
      //     console.log(err)
      //   } else {
      //     // console.log('torrentPoster ' + err + ' ' + buf.toString('base64') + ' ext=' + extension)
      //     this.poster = buf.toString('base64')
      //   }
      // })
    }
    if (!this.infoInterval) {
      this.infoInterval = setInterval(this.updateTorrentStatus.bind(this), 3000);
    }
    this.setState({ shareMsg: '' })
  }

  updateSecret(e) {
    if (e && e.target) {
      this.userSec = e.target.value;
      let rsId = calcAccountId(this.userSec);
      this.setState({ rsId: rsId })
    }
  }

  updateFee(e) {
    if (e && e.target) {
      this.fee = e.target.value;
    }
  }

  updatePrice(e) {
    if (e && e.target) {
      this.price = e.target.value;
    }
  }

  updateTags(e) {
    if (e && e.target) {
      this.tags = e.target.value;
    }
  }

  updateDeadline(e) {
    if (e && e.target) {
      this.deadline = e.target.value;
    }
  }

  renderAddFile() {
    return (<Row>
      <Col xs="12">
        <Fade>
          <Card>
            <CardHeader>
              <i className="icon-share" onClick={this.toggle.bind(this)}>共享资源赚积分</i>
              <div className="card-header-actions">
                <Button color="link" className="card-header-action btn-setting"><i className="icon-settings"></i></Button>
                <Button color="link" className="card-header-action btn-minimize" data-target="#collapseExample" onClick={this.toggle.bind(this)}><i className="icon-arrow-up"></i></Button>
                <Button color="link" className="card-header-action btn-close" onClick={this.toggleFade}><i className="icon-close"></i></Button>
              </div>
            </CardHeader>
            <Collapse isOpen={this.state.collapse} id="collapseExample">
              <CardBody>
                {/* <FormGroup>
                  <Label htmlFor="appendedInputButtons">账户密码:</Label>
                  <div className="controls">
                    <InputGroup>
                      <Input id="appendedInputButtons" size="16" type="text" value={this.secret} onChange={this.updateSecret.bind(this)} placeholder='输入账户密码共享资源' />
                    </InputGroup>
                  </div>
                </FormGroup> */}
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>我的账号:</InputGroupText>
                      </InputGroupAddon>
                      <Input id="prependedInput" size="16" type="text" value={this.state.rsId} />
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>资源:</InputGroupText>
                      </InputGroupAddon>
                      <input name="upload" type="file"></input>
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>资源ID:</InputGroupText>
                      </InputGroupAddon>
                      <Input id="prependedInput1" size="16" type="text" value={this.resId} />
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>资源标签:</InputGroupText>
                      </InputGroupAddon>
                      <Input id="prependedInput2" size="16" type="text" onChange={this.updateTags.bind(this)} placeholder='资源描述' />
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>价格:</InputGroupText>
                      </InputGroupAddon>
                      <Input id="appendedPrependedInput3" size="16" type="text" onChange={this.updatePrice.bind(this)} placeholder=' 资源价格,默认0积分' />
                      <InputGroupAddon addonType="append">
                        <InputGroupText>积分</InputGroupText>
                      </InputGroupAddon>
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>机器人费:</InputGroupText>
                      </InputGroupAddon>
                      <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateFee.bind(this)} placeholder='机器人手续费，最少0.01个积分' />
                      <InputGroupAddon addonType="append">
                        <InputGroupText>积分</InputGroupText>
                      </InputGroupAddon>
                    </InputGroup>
                  </div>
                </FormGroup>
                {
                  this.renderDeadline()
                }
                <div>
                  <Alert color="info">{this.state.shareMsg}</Alert>
                  {
                    this.renderUploadAgainButton()
                  }
                </div>
                <div className="form-actions">
                  <Button type="submit" color="primary" disabled={!this.state.ready} onClick={(e) => {
                    this.setState({ shareMsg: '文件处理中...', })
                    setTimeout(() => {
                      this.doShare()
                    }, 1000);
                  }}>分享</Button>
                  <Button color="secondary" onClick={(e) => this.cancelShare()}>取消</Button>
                </div>
              </CardBody>
            </Collapse>
          </Card>
        </Fade>
      </Col>
    </Row >)
  }

  renderUploadAgainButton() {
    if (!this.state.uploadAgain)
      return null
    return (
      <Button type="submit" color="primary" onClick={(e) => this.uploadAgain()}>重新上传</Button>
    )
  }

  renderDeadline() {
    if (true)
      return null;
    return (<FormGroup>
      <div className="controls">
        <InputGroup className="input-prepend">
          <InputGroupAddon addonType="prepend">
            <InputGroupText>有效期:</InputGroupText>
          </InputGroupAddon>
          <Input id="appendedPrependedInput" size="16" onChange={this.updateDeadline.bind(this)} type="text" placeholder='交易有效期，默认5分钟' />
          <InputGroupAddon addonType="append">
            <InputGroupText>分钟</InputGroupText>
          </InputGroupAddon>
        </InputGroup>
      </div>
    </FormGroup>)
  }

  renderResourceItems() {
    let items = this.state.resourceList.map((res) => {
      let pid = 'pid' + res.id;
      let sid = 'sid' + res.id;
      return (<ListGroupItem action>
        <ListGroupItemHeading> {res.name}  <PopoverItem id={res.id} res={res} /></ListGroupItemHeading>
        <ListGroupItemText>
          <code> <span id={pid}></span>节点</code>. &#x2197;<code><span id={sid}></span>上传速度</code>
        </ListGroupItemText>
      </ListGroupItem>)
    })
    return items;
  }

  renderShareList() {
    return (<Row>
      <Col sm="12" xl="6">
        <Card>
          <CardHeader>
            <i className="fa fa-align-justify"></i><strong>我的资源列表</strong>
            <Alert color="danger"><small>请不要刷新或关闭此页面，保持此页面打开状态下，购买过资源的用户才可以下载本机资源</small></Alert>
          </CardHeader>
          <CardBody>
            <ListGroup>
              {this.renderResourceItems()}
            </ListGroup>
          </CardBody>
        </Card>
      </Col>
    </Row>)
  }

  render() {
    return (
      <div className="animated fadeIn">
        {this.renderAddFile()}
        {this.renderShareList()}
      </div>

    );
  }
}

class PopoverItem extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      popoverOpen: false
    };
  }

  toggle() {
    this.setState({
      popoverOpen: !this.state.popoverOpen,
    });
  }

  render() {
    return (
      <span>
        <span className='alert-primary  fade show' id={'Popover-' + this.props.id} onClick={this.toggle}>
          (分享链接)
          </span>
        <Popover placement='top' isOpen={this.state.popoverOpen} target={'Popover-' + this.props.id} toggle={this.toggle}>
          <PopoverHeader>分享这个资源的链接</PopoverHeader>
          <PopoverBody>{getCurrentHost() + "/#/btb#" + this.props.res.id + '#' + this.props.res.name}</PopoverBody>
        </Popover>
      </span>
    );
  }
}

export default ResourceShare;
